class=\"\">\r\n                            <div>\r\n                                <div\r\n                                    style=\"\r\n                                        flex: 1;\r\n                                        display: flex;\r\n                                        justify-content: flex-end;\r\n                                    \"\r\n                                >\r\n                                    <a @click=\"clicked($event)\">\r\n                                        <div class=\"addCatProduct\">\r\n                                            Add New\r\n                                            {{ selectedCatName }}\r\n                                        </div>\r\n                                    </a>\r\n                                </div>\r\n                                <div v-if=\"catProducts != null\">\r\n                                    <div class=\"wed-venue-items\">\r\n                                        <div\r\n                                            v-for=\"(item, index) in catProducts\"\r\n                                            :key=\"index\"\r\n                                            style=\"\r\n                                                transition: opacity 1s;\r\n                                                opacity: 1;\r\n                                            \"\r\n                                        >\r\n                                            <a\r\n                                                :href=\"\r\n                                                    '/services/' +\r\n                                                    vendorDetails.slug +\r\n                                                    '/' +\r\n                                                    vendorCategory.path +\r\n                                                    '/' +\r\n                                                    item.slug\r\n                                                \"\r\n                                            >\r\n                                                <div>\r\n                                                    <div>\r\n                                                        <img\r\n                                                            v-if=\"\r\n                                                                item.images !=\r\n                                                                null\r\n                                                            \"\r\n                                                            :src=\"\r\n                                                                '/assets/vendors/uploads/' +\r\n                                                                item.images[0]\r\n                                                            \"\r\n                                                            alt=\"\"\r\n                                                            width=\"100px\"\r\n                                                            height=\"auto\"\r\n                                                        />\r\n                                                        <img\r\n                                                            v-else\r\n                                                            src=\"/assets/vendors/landing/Frame870.png\"\r\n                                                            alt=\"\"\r\n                                                            width=\"100px\"\r\n                                                            height=\"auto\"\r\n                                                        />\r\n                                                        <div\r\n                                                            class=\"wed-venue-items-desc\"\r\n                                                        >\r\n                                                            <div>\r\n                                                                <div>\r\n                                                                    {{\r\n                                                                        item.name\r\n                                                                    }}\r\n                                                                </div>\r\n                                                                <div\r\n                                                                    class=\"d-flex\"\r\n                                                                >\r\n                                                                    <div>5</div>\r\n                                                                    <div>\r\n                                                                        <img\r\n                                                                            src=\"/assets/icon-SVG/Star.svg\"\r\n                                                                            alt=\"\"\r\n                                                                            style=\"\r\n                                                                                margin-top: -5px;\r\n                                                                            \"\r\n                                                                        />\r\n                                                                    </div>\r\n                                                                </div>\r\n                                                            </div>\r\n                                                            <div>\r\n                                                                <p>location</p>\r\n                                                                <p>\r\n                                                                    30 reviews\r\n                                                                </p>\r\n                                                            </div>\r\n                                                        </div>\r\n                                                    </div>\r\n                                                </div>\r\n                                                <div\r\n                                                    class=\"wed-venue-items-price\"\r\n                                                >\r\n                                                    <div>Starting from</div>\r\n                                                    <div>\r\n                                                        10000\r\n                                                        <span>per day</span>\r\n                                                    </div>\r\n                                                </div>\r\n                                            </a>\r\n                                            <div\r\n                                                style=\"\r\n                                                    padding: 10px;\r\n                                                    display: flex;\r\n                                                    justify-content: space-between;\r\n                                                \"\r\n                                            >\r\n                                                <button\r\n                                                    @click=\"edit(item.id)\"\r\n                                                    type=\"button\"\r\n                                                    :data-id=\"item.id\"\r\n                                                >\r\n                                                    EDIT\r\n                                                </button>\r\n                                                <!-- <button\r\n                                                    @click=\"\r\n                                                        removeProd(\r\n                                                            $event,\r\n                                                            item.id\r\n                                                        )\r\n                                                    \"\r\n                                                    type=\"button\"\r\n                                                    :data-id=\"item.id\"\r\n                                                >\r\n                                                    DELETE\r\n                                                </button> -->\r\n                                            </div>\r\n                                        </div>\r\n                                    </div>\r\n                                </div>\r\n                            </div>\r\n                        </div>\r\n                    </div>\r\n                </div>\r\n            </div>\r\n        </form>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n    name: \"services\",\r\n    props: [\"vendorCat\", \"vendor\", \"added\"],\r\n    data() {\r\n        return {\r\n            form: false,\r\n            route: null,\r\n            sendCategory: [],\r\n            defaultCategories: null,\r\n            selectedCat: null,\r\n            prodDisplay: false,\r\n            showDisp: false,\r\n            catProducts: null,\r\n            vendorDetails: JSON.parse(this.vendor),\r\n            selectedCatName: null,\r\n            vendorCategory: JSON.parse(this.vendorCat),\r\n        };\r\n    },\r\n    methods: {\r\n        clicked(e) {\r\n            this.$emit(\"clickedAdd\");\r\n        },\r\n        edit(e) {\r\n            console.log(e);\r\n            this.$emit(\"edit\", e);\r\n        },\r\n        removeProd(evt, e) {\r\n            //console.log(evt.currentTarget.parentElement.parentElement);\r\n            let itm = evt.currentTarget.parentElement.parentElement;\r\n            let _this = this;\r\n            let link = null;\r\n            if (_this.vendorCategory.name == \"Accommodation\") {\r\n                link = \"addacc\";\r\n            }\r\n            if (_this.vendorCategory.name == \"Venues\") {\r\n                link = \"addvenue\";\r\n            }\r\n            //console.log(`/api/vendor/${link}/${e}`, _this.vendorCategory.name);\r\n            axios\r\n                .delete(`/api/vendor/${link}/${e}`)\r\n                .then(function (response) {\r\n                    let id = _this.catProducts.findIndex((x) => x.id === e);\r\n                    itm.style.opacity = 0;\r\n                    _this.$emit(\"deleted\");\r\n                    setTimeout(function () {\r\n                        _this.catProducts.splice(id, 1);\r\n                        itm.style.opacity = 1;\r\n                    }, 1500);\r\n                    //fruits.splice(2);\r\n                    // _this.getProducts(\"e\");\r\n                })\r\n                .catch((error) => {\r\n                    console.log(error.response);\r\n                });\r\n        },\r\n        getProducts(e) {\r\n            var _this = this;\r\n            this.selectedCatName = this.vendorCategory.name;\r\n            this.showDisp = false;\r\n            _this.prodDisplay = true;\r\n            let v = this.vendorDetails.id;\r\n            let catid = this.vendorCategory.id;\r\n            console.log(v, catid);\r\n            axios\r\n                .get(`/api/vendor/vendorsProducts/${v}/${catid}`)\r\n                .then(function (response) {\r\n                    console.log(response.data);\r\n                    _this.route = `/services/${_this.vendorDetails.slug}/${response.data[1]}`;\r\n\r\n                    if (response.data[0] != 0) {\r\n                        _this.catProducts = response.data[0];\r\n                    } else {\r\n                        _this.catProducts = null;\r\n                        _this.showDisp = true;\r\n                    }\r\n                })\r\n                .catch((